import React, { useState, ChangeEvent, FormEvent } from 'react';
import styled from 'styled-components';
import { SearchButton } from '../MainPages/InTicketReservation/ TicketReservation';
import { useHistory } from 'react-router-dom';
import { Container } from '../MainPages/InTicketReservation/FlightSelect';

export const Form = styled.form`
  width: 300px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  background-color: rgba(255, 255, 255, 0.6);
`;

export const FormGroup = styled.div`
  margin-bottom: 20px;
`;

export const Label = styled.label`
  display: block;
  margin-bottom: 5px;
`;

export const Input = styled.input`
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
`;

export const SubmitDiv = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
`;

export const FormButton = styled.button`
  background-color: rgba(128, 145, 171, 0.82);
  color: white;
  padding: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;

  &:hover {
    background-color: #677486;
  }
`;

function RegisterForm() {
  const registerhistory = useHistory();

  const [formData, setFormData] = useState({
    username: '',
    password: '',
    korName: '',
    engName: '',
    birth: '',
    phone: '',
    email: '',
  });

  const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    //         .
    //  
    if (
      !formData.username ||
      !formData.password ||
      !formData.korName ||
      !formData.engName ||
      !formData.birth ||
      !formData.phone ||
      !formData.email
    ) {
      alert('  .');
      return;
    }

    //  
    const passwordRegex =
      /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{6,}$/;
    if (!passwordRegex.test(formData.password)) {
      alert(' 6 , , ,   .');
      return;
    }

    //  
    const phoneRegex = /^010-\d{4}-\d{4}$/;
    if (!phoneRegex.test(formData.phone)) {
      alert(' 010-0000-0000  .');
      return;
    }
    console.log('Form Data Submitted:', formData);

    fetch('/register', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(formData),
    })
      .then((response) => response.json())
      .then((data) => {
        console.log('Server response:', data);

        //      
        registerhistory.push('/Login');
      })
      .catch((error) => {
        console.error('Error sending data to server:', error);
      });
  };

  return (
    <Container>
      <Form onSubmit={handleSubmit}>
        <FormGroup>
          <Label htmlFor="username">:</Label>
          <Input
            type="text"
            id="username"
            name="username"
            value={formData.username}
            onChange={handleChange}
          />
        </FormGroup>
        <FormGroup>
          <Label htmlFor="password">:</Label>
          <Input
            type="password"
            id="password"
            name="password"
            value={formData.password}
            onChange={handleChange}
          />
        </FormGroup>
        <FormGroup>
          <Label htmlFor="korName"> :</Label>
          <Input
            type="text"
            id="korName"
            name="korName"
            value={formData.korName}
            onChange={handleChange}
          />
        </FormGroup>
        <FormGroup>
          <Label htmlFor="engName"> :</Label>
          <Input
            type="text"
            id="engName"
            name="engName"
            value={formData.engName}
            onChange={handleChange}
          />
        </FormGroup>
        <FormGroup>
          <Label htmlFor="birth">:</Label>
          <Input
            type="date"
            id="birth"
            name="birth"
            value={formData.birth}
            onChange={handleChange}
          />
        </FormGroup>
        <FormGroup>
          <Label htmlFor="phoner">:</Label>
          <Input
            type="tel"
            id="phone"
            name="phone"
            value={formData.phone}
            onChange={handleChange}
            placeholder="010-0000-0000"
          />
        </FormGroup>
        <FormGroup>
          <Label htmlFor="email">:</Label>
          <Input
            type="email"
            id="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
          />
        </FormGroup>
        <SubmitDiv>
          <FormButton type="submit"> </FormButton>
        </SubmitDiv>
      </Form>
    </Container>
  );
}

export default RegisterForm;
